import React, {createContext, useContext, useEffect, useState} from "react";
import {
    Box,
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    TextField
} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import styled from "styled-components";
import SizeQtyGroup from "./SizeQtyGroup";
import {VariantContext} from "./AddItem";

const AddNewVariant = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(0, 0, 0, 0.6);
    padding: 1em;
    border-radius: 15px;
    cursor: pointer;
    border: 2px solid rgba(0, 0, 0, 0.2);
    transition: all 0.2s ease-in-out;

    &:hover {
        border: 2px solid #1976d2;
        color: #1976d2;
    }
`;

const thumbnailStyle = {
    borderRadius: "10px",
    maxWidth: "100px",
    maxHeight: "100px",
    minWidth: "50px",
    minHeight: "50px",
    width: "100%",
    height: "100%",
    objectFit: 'cover',
    boxShadow: "1px 1px 10px rgba(0,0,0,0.2)",
};

const SizeQty = {
    5: 0,
    5.5: 0,
    6: 0,
    6.5: 0,
    7: 0,
    7.5: 0,
    8: 0,
    8.5: 0,
    9: 0,
    9.5: 0,
    10: 0,
    10.5: 0,
    11: 0,
    11.5: 0,
    12: 0,
    12.5: 0,
    13: 0,
}

export const SizeQtyContext = createContext();

export default function AddVariant(props) {

    const [images, setImages] = useState({image1: '', image2: '', image3: '', image4: ''});

    const [sizeQty, setSizeQty] = useState(SizeQty);

    const [selectedSize, setSelectedSize] = useState(5);

    const [qty, setQty] = useState(0);

    const {variants, setVariants} = useContext(VariantContext);

    const [newVariant, setNewVariant] = useState({});

    const [variantName, setVariantName] = useState('');

    const [nameError, setNameError] = useState(false);

    const [open, setOpen] = React.useState(false);


    const handleImageChange = (e) => {
        const {name, value} = e.target;
        setImages(prevState => ({...prevState, [name]: value}));
    };

    const handleNameChange = (e) => {
        setVariantName(e.target.value);
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        if (variantName.trim() === "") {
            return;
        }

        if (variants.find(variant => variant.name === variantName)) {
            return;
        }

        setVariants([...variants, newVariant]);
        handleClose();
    };

    const handleClose = () => {
        setImages({image1: '', image2: '', image3: '', image4: ''});
        setSizeQty(SizeQty);
        setSelectedSize(5);
        setOpen(false);
    };

    useEffect(() => {
        setNewVariant({name: variantName, images: images, sizeQty: sizeQty});
    }, [images, sizeQty]);

    useEffect(() => {
        if (variants.find(variant => variant.name === variantName) || variantName.trim() === "") {
            setNameError(true);
            return
        }
        setNameError(false);
    }, [variantName]);


    return (
        <React.Fragment>
            <AddNewVariant onClick={handleClickOpen}>
                <AddIcon fontSize='large'/>
                <span>Add Variant</span>
            </AddNewVariant>
            <Dialog
                open={open}
                onClose={handleClose}
                PaperProps={{
                    component: 'form',
                }}
            >
                <DialogTitle>Add New Variant</DialogTitle>
                <DialogContent>
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        label="Variant Name"
                        type="text"
                        fullWidth
                        variant="standard"
                        onChange={handleNameChange}
                        error={nameError}
                        helperText={nameError ? "Invalid Variant Name" : ""}
                    />
                    <DialogContentText p="1em 0 0">
                        Please Copy and Paste the Image urls here.
                    </DialogContentText>
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        name="image1"
                        label="Image 1"
                        type="url"
                        fullWidth
                        variant="standard"
                        onChange={handleImageChange}
                        error={images.image1.trim() === ""}
                        helperText={images.image1.trim() === "" ? "Enter image 1 URL" : ""}
                    />
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        name="image2"
                        label="Image 2"
                        type="url"
                        fullWidth
                        variant="standard"
                        onChange={handleImageChange}
                        error={images.image3.trim() === ""}
                        helperText={images.image3.trim() === "" ? "Enter image 2 URL" : ""}
                    />
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        name="image3"
                        label="Image 3"
                        type="url"
                        fullWidth
                        variant="standard"
                        onChange={handleImageChange}
                        error={images.image3.trim() === ""}
                        helperText={images.image3.trim() === "" ? "Enter image 3 URL" : ""}
                    />
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        name="image4"
                        label="Image 4"
                        type="url"
                        fullWidth
                        variant="standard"
                        onChange={handleImageChange}
                        error={images.image4.trim() === ""}
                        helperText={images.image4.trim() === "" ? "Enter image 4 URL" : ""}
                    />
                    <Box component='div'
                         sx={{
                             display: 'flex',
                             flexWrap: 'wrap',
                             justifyContent: 'space-around',
                             paddingTop: '1em',
                             gap: '10px'
                         }}>
                        <img src={images["image1"]} alt="image 1" style={thumbnailStyle}/>
                        <img src={images["image2"]} alt="image 2" style={thumbnailStyle}/>
                        <img src={images["image3"]} alt="image 3" style={thumbnailStyle}/>
                        <img src={images["image4"]} alt="image 4" style={thumbnailStyle}/>
                    </Box>
                    <DialogContentText p={"1em 0 0"}>
                        Please Select the Size and Enter the Quantity here.
                    </DialogContentText>
                    <SizeQtyContext.Provider value={{selectedSize, setSelectedSize, qty, setQty, sizeQty, setSizeQty}}>
                        <SizeQtyGroup/>
                    </SizeQtyContext.Provider>
                </DialogContent>
                <DialogActions>
                    <Button type="button" onClick={handleClose}>Cancel</Button>
                    <Button type="submit" onClick={handleSubmit}>Add</Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    );
}