import React from "react";
import styled from "styled-components";
import {Dialog, DialogActions, DialogContent, DialogTitle, TextField} from "@mui/material";
import Button from "../../Components/Button";
import {Close} from "@mui/icons-material";


const UserProfileWrapper = styled.div`
    background: #f9f9f9;
    min-height: calc(100vh - 77px - 62px);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2em 0;
    position: relative;

    &::before {
        content: "";
        background-image: url("https://images.pexels.com/photos/8473454/pexels-photo-8473454.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
        background-size: cover;
        background-position: center;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        height: 250px;
        width: 100%;
    }

    @media (max-width: 600px) {
        min-height: calc(100vh - 60px - 62px);
    }

    .user-details-container {
        padding: 2em;
        z-index: 3;
        width: 100%;
        max-width: 800px;
        /* From https://css.glass */
        background: rgba(255, 255, 255, 0.5);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
        backdrop-filter: blur(25px);
        -webkit-backdrop-filter: blur(25px);
        border: 1px solid rgba(255, 255, 255, 0.3);
    }

    .user-details-container h2 {
        position: relative;
        z-index: 1;
        letter-spacing: 1px;
        text-transform: uppercase;
        font-style: italic;
    }

    .user-details-container h2::after {
        content: "";
        position: absolute;
        z-index: 1;
        width: 100px;
        bottom: 0;
        left: 0;
        border-bottom: 1px solid #000000;
    }

    .user-details {
        padding-top: 1em;
    }

    .user-details-item {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        margin-bottom: 2em;
    }

    .action-buttons {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .action-buttons p {
        cursor: pointer;
        font-size: 15px;
        letter-spacing: 1px;
    }

    .user-details-item label {
        font-size: 15px;
        letter-spacing: 1px;
    }

    .user-details-item input {
        width: 100%;
        padding: 10px 5px;
        background: none;
        border: none;
        border-bottom: 1px solid #8a8a8a;
        font-size: 15px;
        outline: none;
    }
    
    @media (max-width: 400px) {
        .action-buttons {
            flex-direction: column-reverse;
            gap: 26px;
        }
    }

`;

export default function Profile() {
    return (
        <UserProfileWrapper>
            <div className="user-details-container">
                <h2>User Profile</h2>
                <div className="user-details">
                    <form>
                        <div className="user-details-item">
                            <label>Name</label>
                            <input type="text" value="Dilanka" readOnly/>
                        </div>
                        <div className="user-details-item">
                            <label>Email</label>
                            <input type="email" value="dilankayasuru001@gmail.com" readOnly/>
                        </div>
                        <div className="user-details-item">
                            <label>Phone Number</label>
                            <input type="text" value="0712728057" readOnly/>
                        </div>
                        <div className="user-details-item">
                            <label>Address</label>
                            <input type="text" value="2/3/G, Sri Anomadassi Mw, Ambalangoda, Polgasowita" readOnly/>
                        </div>
                        <div className="action-buttons">
                            <ChangePassword/>
                            <Button type="submit">Save Changes</Button>
                        </div>
                    </form>
                </div>
            </div>
        </UserProfileWrapper>
    )
}


function ChangePassword() {

    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <React.Fragment>
            <p onClick={handleClickOpen}>Change Password</p>
            <Dialog
                open={open}
                onClose={handleClose}
                PaperProps={{
                    component: 'form',
                    onSubmit: (event) => {
                        event.preventDefault();
                        // const formData = new FormData(event.currentTarget);
                        // const formJson = Object.fromEntries(formData.entries());
                        // const email = formJson.email;
                        // console.log(email);
                        handleClose();
                    },
                }}
            >
                <div className="dialog-top-header" style={{display:"flex",justifyContent:"space-between", alignItems: "center"}}>
                    <DialogTitle>Create New Password</DialogTitle>
                    <Close sx={{marginRight: "24px"}} onClick={handleClose}/>
                </div>

                <DialogContent>
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        id="current-password"
                        name="current-password"
                        label="Current Password"
                        type="password"
                        fullWidth
                        variant="standard"
                    />
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        id="new-password"
                        name="new-password"
                        label="New Password"
                        type="password"
                        fullWidth
                        variant="standard"
                    />
                    <TextField
                        autoFocus
                        required
                        margin="dense"
                        id="re-new-password"
                        name="re-new-password"
                        label="Re-Enter New Password"
                        type="password"
                        fullWidth
                        variant="standard"
                    />

                </DialogContent>
                <DialogActions sx={{justifyContent: "center"}}>
                    <Button type="submit">Change Password</Button>
                </DialogActions>
            </Dialog>

        </React.Fragment>
    )
}