export default function ProductDetails(props) {

    const [shoeColor, setShoeColor] = useState(Product.images[0]);
    const [mainImage, setMainImage] = useState(shoeColor.prevImages[0])

    useEffect(() => {
        setMainImage(shoeColor.prevImages[0])
    }, [shoeColor]);

    return (
        <React.Fragment>
            <Navbar/>
            <section className="product-details-container">

                <div className="product-details-left">
                    <div className="product-image-container-outer">
                        <div className="product-image-container">
                            <img
                                src={mainImage}
                                alt="prodcut image"/>
                        </div>
                        <div className="product-preview-images-container">
                            {
                                shoeColor.prevImages.map(
                                    (image) => <img src={image} alt="shoe image" onMouseOver={
                                        () => setMainImage(image)
                                    }/>
                                )
                            }
                        </div>
                    </div>
                </div>

                <div className="product-details-right">
                    <div className="product-details">
                        <div className="product-name-text">
                            <h1>{Product.name}</h1>
                            <p className="product-name-secondary">{Product.subHeading}</p>
                            <p className="product-price">${Product.price}</p>
                        </div>
                        <div className="select-color-container">
                            <p className="sub-heading">Select Color</p>
                            <div className="select-color-image-container">
                                {
                                    Product.images.map(
                                        (product) => <img
                                            src={product.colorImage} alt="shoe color"
                                            onClick={() => {
                                                setShoeColor(product);
                                            }}
                                        />
                                    )
                                }
                            </div>
                        </div>
                        <div className="select-size-container-outer">
                            <p className="sub-heading">Select Size</p>
                            <div className="select-size-container">
                                {
                                    Product.sizes.map(
                                        (size) => <p>{size}</p>
                                    )
                                }
                            </div>
                        </div>
                        <div className="product-details-button-container">
                            <Button>Buy Now</Button>
                            <Button>Add to Cart <ShoppingCartRoundedIcon/></Button>
                        </div>
                        <div className="product-description-text">
                            <p className="sub-heading">Description</p>
                            <p>{Product.description}</p>
                        </div>
                    </div>
                    <Reviews data={Product.reviews}/>
                </div>

            </section>
            <Footer/>
        </React.Fragment>
    )
}