import React, {useEffect, useState} from "react";
import "./Navbar.css";
import ShoppingCartRoundedIcon from '@mui/icons-material/ShoppingCartRounded';
import {Close} from "@mui/icons-material";
import {Link} from "react-router-dom";
import Logo from "../../Components/Logo";
import Button from "../../Components/Button";
import PersonIcon from '@mui/icons-material/Person';
import MenuRoundedIcon from '@mui/icons-material/MenuRounded';
import ShoppingCart from "../../Components/ShoppingCart";
import styled from "styled-components";
import LogOut from "../../Components/LogOut";

const CartItems = [
    {
        product: "Celestial Strides",
        size: 5,
        price: 150,
        qty: 1,
        image: "https://images.unsplash.com/photo-1525966222134-fcfa99b8ae77?q=80&w=1898&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D",
    },
    {
        product: "Enchanted Soles",
        size: 10,
        price: 110,
        qty: 2,
        image: "https://images.unsplash.com/photo-1595950653106-6c9ebd614d3a?q=80&w=1887&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D",
    },
    {
        product: "Samba Shoes",
        size: 8,
        price: 140,
        qty: 1,
        image: "https://assets.adidas.com/images/w_383,h_383,f_auto,q_auto,fl_lossy,c_fill,g_auto/0e3b7e547de04961b8af600b44b2c965_9366/samba-xlg-shoes.jpg",
    },
    {
        product: "Celestial Strides",
        size: 5,
        price: 150,
        qty: 1,
        image: "https://images.unsplash.com/photo-1525966222134-fcfa99b8ae77?q=80&w=1898&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D",
    },
    {
        product: "Enchanted Soles",
        size: 10,
        price: 110,
        qty: 2,
        image: "https://images.unsplash.com/photo-1595950653106-6c9ebd614d3a?q=80&w=1887&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D",
    },
    {
        product: "Samba Shoes",
        size: 8,
        price: 140,
        qty: 1,
        image: "https://assets.adidas.com/images/w_383,h_383,f_auto,q_auto,fl_lossy,c_fill,g_auto/0e3b7e547de04961b8af600b44b2c965_9366/samba-xlg-shoes.jpg",
    },
];

const CartContainer = styled.div`
    background: #ffffff;
    height: 100vh;
    min-width: 450px;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1001;
    overflow-y: scroll;
    scrollbar-width: none;
    box-shadow: 4px 4px 25px 10px rgba(0, 0, 0, .2);
    transition: all .5s ease-in-out;

    .cart-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1em;
        position: sticky;
        top: 0;
        right: 0;
        width: 100%;
        background: #ffffff;
        box-shadow: 0 0 25px 0 rgba(0, 0, 0, .3);
    }

    .cart-top p {
        font-size: 1.3em;
    }

    @media screen and (max-width: 450px) {
        min-width: 0;
        width: 100%;
    }
`;

export default function Navbar() {

    const [showMenu, setShowMenu] = useState(false);
    const [showCart, setShowCart] = useState(false);

    const [token, setToken] = useState('');

    const mobileNavShow = {
        transform: "translateX(0)"
    }

    const mobileNavHide = {
        transform: "translateX(350px)"
    }

    const blur = {
        display: `${showMenu || showCart ? "block" : "none"} `,
        backdropFilter: "blur(5px)",
        position: "fixed",
        top: "0",
        left: "0",
        height: "100vh",
        width: "100vw",
        zIndex: "1000",
    }

    useEffect(() => {
        const token = localStorage.getItem('jwtToken');
        setToken(token);
    }, []);

    return (
        <>
            <section>
                <div className="desktop-nav">
                    <Link to={"/"}>
                        <Logo/>
                    </Link>
                    <nav>
                        <ul>
                            <li><span><Link to="/men">Men</Link></span></li>
                            <li><span><Link to="/women">Women</Link></span></li>
                            <li><span><Link to="/kids">Kids</Link></span></li>
                            <li><span><Link to="/shop">Shop</Link></span></li>
                        </ul>
                    </nav>
                    <div className="button-container">
                        <Link to="/login">
                            {token ? <LogOut/> : <button className="signIn-button">Sign In</button>}
                        </Link>
                        <div className="shoppingCart" onClick={() => setShowCart(!showCart)}>
                            <ShoppingCartRoundedIcon/>
                        </div>
                    </div>
                </div>
                <div className="mobile-nav-top">
                    <Link to={"/"}>
                        <Logo/>
                    </Link>
                    <div className="mobile-nav-top-icon-container">
                        <div className="mobile-nav-top-icon">
                            <Link to="/login">
                                <PersonIcon
                                    sx={
                                        {
                                            fontSize: "2.2em",
                                        }
                                    }
                                />
                            </Link>
                        </div>
                        <div className="mobile-nav-top-icon" onClick={() => setShowCart(!showCart)}>
                            <ShoppingCartRoundedIcon
                                sx={
                                    {
                                        fontSize: "2em",
                                    }
                                }
                            />
                        </div>
                        <div className="mobile-nav-top-icon" onClick={() => setShowMenu(!showMenu)}>
                            <MenuRoundedIcon
                                sx={
                                    {
                                        fontSize: "2.2em",
                                    }
                                }
                            />
                        </div>
                    </div>
                </div>
                <div className="mobile-nav" style={showMenu ? mobileNavShow : mobileNavHide}>
                    <div className="close-btn">
                        <Close fontSize="large" className="close-btn" onClick={() => setShowMenu(!showMenu)}/>
                    </div>
                    <nav>
                        <ul>
                            <li onClick={() => setShowMenu(!showMenu)}><span><Link to="/men">Men</Link></span></li>
                            <li onClick={() => setShowMenu(!showMenu)}><span><Link to="/women">Women</Link></span></li>
                            <li onClick={() => setShowMenu(!showMenu)}><span><Link to="/kids">Kids</Link></span></li>
                            <li onClick={() => setShowMenu(!showMenu)}><span><Link to="/shop">Shop</Link></span></li>
                        </ul>
                    </nav>
                    <div className="login-container">
                        <div className="mobile-nav-text">
                            <p>
                                Ready to unlock exclusive benefits? Sign in or join us today!
                            </p>
                        </div>
                        <div className="mobile-nav-buttons">
                            <Button primary={false} onClick={() => setShowMenu(!showMenu)}><Link to="/login">Join
                                Us</Link></Button>
                            <Button onClick={() => setShowMenu(!showMenu)}><Link to="/login">Sign In</Link></Button>
                        </div>
                    </div>
                </div>
            </section>
            <CartContainer style={showCart ? {transform: "translateX(0)"} : {transform: "translateX(150%)"}}>
                <div className="cart-top">
                    <p>My Cart</p>
                    <Close fontSize="large" onClick={() => setShowCart(!showCart)} cursor="pointer"/>
                </div>
                <ShoppingCart cartItems={CartItems}/>
            </CartContainer>
            <div className="blur" style={blur}></div>
        </>
    )
}