import React, {useContext, useEffect} from 'react';
import {
    FormControl,
    InputLabel, Select, OutlinedInput, Box, TextField
} from '@mui/material';
import {SizeQtyContext} from "./AddVariant";


export default function SizeQtyGroup(props) {

    const {selectedSize, setSelectedSize, qty, setQty, sizeQty, setSizeQty} = useContext(SizeQtyContext);

    const handleSizeChange = (e) => {
        setSelectedSize(e.target.value);
    };

    const handleQtyChange = (e) => {
        setQty(e.target.value);
    };

    useEffect(() => {
        setQty(sizeQty[selectedSize]);
    }, [selectedSize]);

    useEffect(() => {
        setSizeQty((prevState) => ({...prevState, [selectedSize]: qty}));
    }, [qty]);

    return (
        <Box component='div' sx={{display: 'flex', flexWrap: 'wrap', justifyContent: 'center', paddingTop: '1em'}}>
            <FormControl sx={{m: 1, minWidth:50 ,maxWidth: 120}}>
                <InputLabel htmlFor="demo-dialog-native">Size</InputLabel>
                <Select
                    native
                    defaultValue={selectedSize}
                    onChange={handleSizeChange}
                    input={<OutlinedInput label="Age" id="demo-dialog-native"/>}
                >
                    {
                        Object.keys(sizeQty).map((key) => {
                            return <option key={key} value={key}>{key}</option>
                        })
                    }
                </Select>
            </FormControl>
            <FormControl sx={{m: 1, minWidth: 50, maxWidth: 120}}>
                <TextField
                    label="Qty"
                    id="qty"
                    placeholder="Enter Qty"
                    value={qty}
                    variant="standard"
                    type='number'
                    InputProps={{ inputProps: { min: 0 } }}
                    onChange={handleQtyChange}
                />
            </FormControl>
        </Box>
    );
};