import React from "react";
import styled from "styled-components";
import {FormControl, TextField} from "@mui/material";
import Button from "./Button";
import Footer from "../Sections/Footer/Footer";
import Navbar from "../Sections/Navbar/Navbar";

const Heading = styled.h2`
    letter-spacing: 1px;
    font-weight: 300;
    font-size: 20px;
    text-transform: uppercase;
    font-style: italic;
    position: relative;
    margin-bottom: 25px;

    &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -3px;
        width: 100px;
        border-bottom: 1px solid #000000;
    }
`;

const OrderItem = styled.div`
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;

    .order-details {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1em;
    }

    .order-details img {
        width: 70px;
        height: 70px;
        object-fit: cover;
        object-position: center;
        border-radius: 10px;
        box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
    }

    .text-details p {
        //padding: 1px 0;
        line-height: 22px;
    }

    .item-price {
        font-size: 22px;
    }
`;

const CheckoutWrapper = styled.div`
    min-height: 100vh;
    padding-top: calc(77px + 2em);
    padding-bottom: 2em;
    display: flex;
    justify-content: center;
    gap: 10px;

    @media (max-width: 600px) {
        padding-top: calc(60px + 2em);
    }
    
    @media (max-width: 800px) {
        flex-direction: column;
        align-items: center;
        gap: 2em;
    }

    p {
        line-height: 1.3em;
    }
`;

const CheckoutLeft = styled.div`
    width: 100%;
    padding: 0 1em;
    max-width: 450px;

    .amount-bar {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        padding: 1em 0;
    }
    
    .left-inner {
        position: sticky;
        top: 109px;
    }
`;

const CheckoutRight = styled.form`
    width: 100%;
    padding: 0 1em;
    max-width: 450px;
`;


export default function Checkout() {

    const handleSubmit = (e) => {
        e.preventDefault();
    }

    return (
        <React.Fragment>
            <Navbar/>
            <CheckoutWrapper>
                <CheckoutLeft>
                    <div className="left-inner">
                        <Heading>Order Details</Heading>
                        <div style={{paddingBottom: "20px"}}>
                            <p style={{opacity: "60%"}}>Total</p>
                            <p style={{fontSize: "24px"}}>${450}</p>
                        </div>
                        <OrderItem>
                            <div className="order-details">
                                <img
                                    src="https://static.nike.com/a/images/t_PDP_144_v1/f_auto,u_126ab356-44d8-4a06-89b4-fcdcc8df0245,c_scale,fl_relative,w_1.0,h_1.0,fl_layer_apply/e7c9aadd-242a-4dd4-8509-6ddf9645525e/air-jordan-1-retro-high-og-mens-shoes-JHpxkn.png"
                                    alt=""/>
                                <div className="text-details">
                                    <p>{"Radiant Glide"}</p>
                                    <p><span style={{opacity: "60%"}}>Size</span> {10}</p>
                                    <p><span style={{opacity: "60%"}}>Qty</span> {10}</p>
                                </div>
                            </div>
                            <div style={{textAlign: "end"}}>
                                <p className="item-price">${150}</p>
                                <p style={{opacity: "60%", fontSize: "12px"}}>${15} each</p>
                            </div>
                        </OrderItem>
                        <OrderItem>
                            <div className="order-details">
                                <img
                                    src="https://static.nike.com/a/images/t_PDP_144_v1/f_auto,u_126ab356-44d8-4a06-89b4-fcdcc8df0245,c_scale,fl_relative,w_1.0,h_1.0,fl_layer_apply/e7c9aadd-242a-4dd4-8509-6ddf9645525e/air-jordan-1-retro-high-og-mens-shoes-JHpxkn.png"
                                    alt=""/>
                                <div className="text-details">
                                    <p>{"Radiant Glide"}</p>
                                    <p><span style={{opacity: "60%"}}>Size</span> {10}</p>
                                    <p><span style={{opacity: "60%"}}>Qty</span> {10}</p>
                                </div>
                            </div>
                            <div style={{textAlign: "end"}}>
                                <p className="item-price">${150}</p>
                                <p style={{opacity: "60%", fontSize: "12px"}}>${15} each</p>
                            </div>
                        </OrderItem>
                        <div className="amount-bar">
                            <p>Subtotal</p>
                            <p>${500.00}</p>
                        </div>
                        <div className="amount-bar">
                    <span style={{opacity: "60%"}}>
                        <p>Shipping</p>
                        <p>(3-5 business days)</p>
                    </span>
                            <p>${5.00}</p>
                        </div>
                        <div className="amount-bar">
                            <p>Total due</p>
                            <p>${540}</p>
                        </div>
                    </div>
                </CheckoutLeft>
                <CheckoutRight onSubmit={handleSubmit}>
                    <div>
                        <Heading>Shipping Details</Heading>
                        <div>
                            <TextField
                                fullWidth
                                label="Address"
                                required
                                sx={{
                                    marginBottom: "15px",
                                    '& .MuiFormLabel-root': {
                                        fontSize: '0.8rem',
                                    },
                                }}
                            />
                            <TextField
                                fullWidth
                                label="Appartment, suite, etc. (optional)"
                                sx={{
                                    marginBottom: "15px",
                                    '& .MuiFormLabel-root': {
                                        fontSize: '0.8rem',
                                    },
                                }}
                            />
                            <div
                                style={{
                                    display: "flex",
                                    alignItems: "center",
                                    gap: "15px"
                                }}
                            >
                                <TextField
                                    fullWidth
                                    label="City"
                                    required
                                    sx={{
                                        marginBottom: "15px",
                                        '& .MuiFormLabel-root': {
                                            fontSize: '0.8rem',
                                        },
                                    }}
                                />
                                <TextField
                                    fullWidth
                                    label="Postal Code"
                                    required
                                    sx={{
                                        marginBottom: "15px",
                                        '& .MuiFormLabel-root': {
                                            fontSize: '0.8rem',
                                        },
                                    }}
                                />
                            </div>
                            <TextField
                                fullWidth
                                type="tel"
                                label="Phone"
                                sx={{
                                    marginBottom: "15px",
                                    '& .MuiFormLabel-root': {
                                        fontSize: '0.8rem',
                                    },
                                }}
                            />
                        </div>

                    </div>
                    <div style={{paddingTop: "20px"}}>
                        <Heading>Payment Details</Heading>
                        <div>
                            <TextField
                                fullWidth
                                type="text"
                                label="CARDHOLDER NAME"
                                required
                                sx={{
                                    marginBottom: "15px",
                                    '& .MuiFormLabel-root': {
                                        fontSize: '0.8rem',
                                    },
                                }}
                            />
                            <TextField
                                fullWidth
                                type="text"
                                label="CARD NUMBER"
                                required
                                sx={{
                                    marginBottom: "15px",
                                    '& .MuiFormLabel-root': {
                                        fontSize: '0.8rem',
                                    },
                                }}
                            />
                            <div
                                style={{
                                    display: "flex",
                                    alignItems: "center",
                                    gap: "15px"
                                }}
                            >
                                <TextField
                                    fullWidth
                                    label="MOUNTH"
                                    required
                                    type="number"
                                    inputProps={{min: 1, max: 12}}
                                    sx={{
                                        marginBottom: "15px",
                                        '& .MuiFormLabel-root': {
                                            fontSize: '0.8rem',
                                        },
                                    }}
                                />
                                <TextField
                                    fullWidth
                                    label="YEAR"
                                    type="number"
                                    required
                                    sx={{
                                        marginBottom: "15px",
                                        '& .MuiFormLabel-root': {
                                            fontSize: '0.8rem',
                                        },
                                    }}
                                />
                                <TextField
                                    fullWidth
                                    label="CVV"
                                    type="number"
                                    required
                                    inputProps={{min: 0, max: 999}}
                                    sx={{
                                        marginBottom: "15px",
                                        '& .MuiFormLabel-root': {
                                            fontSize: '0.8rem',
                                        },
                                    }}
                                />
                            </div>
                        </div>
                        <Button
                            type="submit"
                            style={{
                                borderRadius: "0",
                                width: "100%",
                                padding: "1em 0",
                                textTransform: "uppercase"
                            }}
                        >
                            Pay now
                        </Button>
                    </div>
                </CheckoutRight>
            </CheckoutWrapper>
            <Footer/>
        </React.Fragment>
    )
}