import React, {useState} from "react";
import {Rating} from "@mui/material";
import styled from "styled-components";
import KeyboardArrowDownRoundedIcon from '@mui/icons-material/KeyboardArrowDownRounded';
import KeyboardArrowUpRoundedIcon from '@mui/icons-material/KeyboardArrowUpRounded';

const ReviewsContainer = styled.div`
    max-width: 400px;

    .reviews-count {
        display: flex;
        justify-content: space-between;
        margin-bottom: 1em;
        align-items: center;
    }

    .reviews-count p {
        font-size: 1.2em;
    }

    .review-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 0.7em;
    }

    .review-user-name {
        padding-bottom: 0.5em;
        font-size: 1.5em;
        font-weight: lighter;
    }

    .review {
        padding-bottom: 1em;
        margin-bottom: 1em;
        border-bottom: 1px solid #000000;
    }
`;

export default function Reviews(props) {

    const [showReviews, setShowReviews] = useState(false);

    const handleClick = () => {
        setShowReviews(!showReviews);
    }

    const style = {
        maxHeight: showReviews ? "none" : "40px",
        overflow: showReviews ? "visible" : "hidden",
    }

    return (
        <ReviewsContainer style={style}>
            <div className="reviews-count">
                <p>Reviews ({props.data.length})</p>
                <div>
                    {showReviews
                        ? <KeyboardArrowUpRoundedIcon sx={{fontSize: "2em", cursor: "pointer"}} onClick={handleClick}/>
                        :
                        <KeyboardArrowDownRoundedIcon sx={{fontSize: "2em", cursor: "pointer"}} onClick={handleClick}/>
                    }
                </div>
            </div>
            {props.data.map(
                (d) =>
                    <div className="review">
                        <div className="review-top">
                            <Rating name="read-only" value={d.rate} readOnly/>
                            <div
                                className="date">{d.date.getDate()}/{d.date.getMonth()}/{d.date.getFullYear()}</div>
                        </div>
                        <p className="review-user-name">
                            {d.name}
                        </p>
                        <div>
                            <p>{d.description}</p>
                        </div>
                    </div>
            )}
        </ReviewsContainer>
    )
}