import React, {useEffect, useState} from "react";
import styled from "styled-components";
import {Close, Mode} from "@mui/icons-material";
import Button from "./Button";
import {Link} from "react-router-dom";
import {useMediaQuery} from "@mui/material";

const OrderInfoWrapper = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    min-height: calc(100vh - 140px);
    width: 100%;
    background: rgba(0, 0, 0, 0.2);
    margin-top: 140px;
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1em;

    @media (max-width: 600px) {
        margin-top: 123px;
        min-height: calc(100vh - 123px);
    }
`;

const OrderDetailsContainer = styled.div`
    background: #ffffff;
    padding: 0 1em 1em;
    border-radius: 15px;
    overflow-y: scroll;
    height: calc(100vh - 200px);
    box-shadow: 1px 1px 15px rgba(0, 0, 0, .5);

    ::-webkit-scrollbar {
        display: none;
    }

    -ms-overflow-style: none;
    scrollbar-width: none;

    .order-details-heading {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 10px;
        position: sticky;
        top: 0;
        background: #ffffff;
        padding-bottom: 10px;
    }

    .order-details-heading h2 {
        letter-spacing: 1px;
        text-transform: uppercase;
        font-style: italic;
        position: relative;
    }

    .order-details-heading h2::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        border-bottom: 1px solid #000000;
        width: 150px;
    }
`;

const OrderDetails = styled.div`
    margin-bottom: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 15px;
    padding: 1em;
    background: #e3e3e3;
    border-radius: 5px;

    @media (max-width: 600px) {
        flex-wrap: wrap;
    }

    div {
        width: 100%;
    }

    p {
        margin-bottom: 5px;
        letter-spacing: 1px;
    }

    & p:last-child {
        color: #484848;
    }
`;

const OrderItemsContainer = styled.div`
    .order-items-table-heading {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        place-items: center;
        padding-bottom: 10px;
        border-bottom: 1px solid #d5d5d5;
        margin-bottom: 10px;
    }
`;

export default function OrderInfo(props) {

    const isMobile = useMediaQuery('(max-width: 600px)');

    return (
        <OrderInfoWrapper>
            <OrderDetailsContainer>
                <div className="order-details-heading">
                    <h2>Order Details</h2>
                    <Close onClick={props.handleClose} cursor="pointer" sx={{fontSize: "26px"}}/>
                </div>
                <div>
                    <OrderDetails>
                        <div>
                            <p>Date Placed</p>
                            <p>{new Date().getDate()}/{new Date().getMonth()}/{new Date().getFullYear()}</p>
                        </div>
                        <div>
                            <p>Order ID</p>
                            <p>{53501}</p>
                        </div>
                        <div>
                            <p>Total</p>
                            <p>${450}</p>
                        </div>
                        <div>
                            <p>Address</p>
                            <p>{"2/3/G, Sri Anomadassi Mw, Ambalangoda, Polgaswita, 10320"}</p>
                        </div>
                    </OrderDetails>
                </div>
                <OrderItemsContainer>
                    {isMobile ? <></> : <div className="order-items-table-heading">
                        <p>Product</p>
                        <p>Price</p>
                        <p>Qty</p>
                        <p>Action</p>
                    </div>}

                    <div className="order-items">
                        <OrderItem/>
                        <OrderItem/>
                        <OrderItem/>
                        <OrderItem/>
                    </div>
                </OrderItemsContainer>
            </OrderDetailsContainer>
        </OrderInfoWrapper>)
}

function OrderItem() {

    const [user, setUser] = useState(false);

    useEffect(() => {
        const location = window.location.href.split('/')[window.location.href.split('/').length - 1];
        setUser(location === 'user');
    }, []);

    const OrderItemRow = styled.div`
        display: grid;
        place-items: center;
        grid-template-columns: repeat(4, 1fr);
        border-bottom: 1px solid #d5d5d5;
        padding-bottom: 10px;
        margin-bottom: 10px;

        .order-item-details {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
        }

        .order-item-image {
            height: 80px;
            width: 80px;
            overflow: hidden;
            border-radius: 5px;
        }

        .order-item-image img {
            width: 100%;
            height: 100%;
            object-position: center;
            object-fit: cover;
        }

        div p span {
            margin-right: 5px;
        }

        @media (max-width: 600px) {
            grid-template-columns: repeat(2, 1fr);
            place-items: flex-start;
            gap: 10px;

            .order-item-image {
                grid-row: 1 / 5;
                width: 100%;
                height: 100%;
            }

        }
    `;

    const isMobile = useMediaQuery('(max-width: 600px)');

    return (<OrderItemRow>
        {isMobile ? <>
            <div className="order-item-image">
                <img
                    src={"https://static.nike.com/a/images/t_PDP_144_v1/f_auto,u_126ab356-44d8-4a06-89b4-fcdcc8df0245,c_scale,fl_relative,w_1.0,h_1.0,fl_layer_apply/acf9cf41-a9bc-4e6a-b98a-7a38bb163f31/air-jordan-1-retro-high-og-yellow-ochre-mens-shoes-JHpxkn.png"}
                    alt="order -item"/>
            </div>
            <div className="order-item-info">
                <p style={isMobile ? {marginBottom: "10px"} : {}}>{"Radiant Glide"}</p>
                <p>Size {10}</p>
            </div>
        </> : <div className="order-item-details">
            <div className="order-item-image">
                <img
                    src={"https://static.nike.com/a/images/t_PDP_144_v1/f_auto,u_126ab356-44d8-4a06-89b4-fcdcc8df0245,c_scale,fl_relative,w_1.0,h_1.0,fl_layer_apply/acf9cf41-a9bc-4e6a-b98a-7a38bb163f31/air-jordan-1-retro-high-og-yellow-ochre-mens-shoes-JHpxkn.png"}
                    alt="order -item"/>
            </div>
            <div className="order-item-info">
                <p>{"Radiant Glide"}</p>
                <p>Size {10}</p>
            </div>
        </div>}


        <div>
            <p>
                {isMobile ? <span>Price</span> : <></>}
                <span>${150}</span>
            </p>
        </div>

        <div>
            <p>
                {isMobile ? <span>Qty</span> : <></>}
                <span>{1}</span>
            </p>
        </div>

        <div style={isMobile ? {
            gridColumn: "span 2",
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            width: "100%"
        } : {
            display: "flex",
            flexDirection: "column",
            gap: "15px",
            alignItems: "center",
            justifyContent: "center",
            width: "100%"
        }}
        >
            {
                user &&
                <span style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                    gap: "5px",
                    borderBottom: "1px solid #333",
                    cursor: "pointer",
                    fontWeight: "300"
                }}><Mode/> Write a review</span>
            }
            <Link to="/product" style={{margin: "0 auto"}}>
                <Button>View Item</Button>
            </Link>
        </div>
    </OrderItemRow>)
}