import React, {createContext, useEffect, useState} from "react";
import {FormControl, InputAdornment, InputLabel, MenuItem, OutlinedInput, Select, TextField} from "@mui/material";
import Button from "../../../Components/Button";
import styled from "styled-components";
import AddVariant from "./AddVariant";
import NewVariant from "./NewVariant";
import axios from "axios";

const DetailsFormContainer = styled.div`

    h2 {
        text-align: center;
        //padding: 0.5em 0;
        font-size: 1.5em;
        font-weight: 300;
        position: relative;
        letter-spacing: 1px;
        text-transform: uppercase;
        font-style: italic;
    }

    form {
        display: flex;
        flex-direction: column;
        max-width: 700px;
        gap: 20px;
        margin: 0 auto;
        padding: 1.5em 1em;
    }

    form .action-buttons {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 20px;
    }

    form .action-buttons button {
        border-radius: 5px;
        min-width: 90px;
    }
`;

export const VariantContext = createContext();

export default function AddItem(props) {

    const [productName, setProductName] = useState('');

    const [subTitle, setSubTitle] = useState('');

    const [price, setPrice] = useState(0);

    const [category, setCategory] = useState('');

    const [description, setDescription] = useState('');

    const [variants, setVariants] = useState([]);

    const [errors, setErrors] = useState({});

    const [product, setProduct] = useState({});

    const handleNameChange = (e) => {
        setErrors((prevState) => ({...prevState, name: false}))
        setProductName(e.target.value)
    };
    const handleTitleChange = (e) => {
        setErrors((prevState) => ({...prevState, subtitle: false}))
        setSubTitle(e.target.value)
    };
    const handlePriceChange = (e) => {
        setErrors((prevState) => ({...prevState, price: false}))
        setPrice(e.target.value)
    };
    const handleCategoryChange = (e) => {
        setCategory(e.target.value)
        setErrors((prevState) => ({...prevState, category: false}))
    };
    const handleDescriptionChange = (e) => {
        setDescription(e.target.value)
        setErrors((prevState) => ({...prevState, description: false}))
    };

    useEffect(() => {
        setProduct({
            name: productName,
            subTitle: subTitle,
            price: price,
            category: category,
            description: description,
            variants: variants
        });
    }, [productName, subTitle, price, category, description, variants]);

    const sendToServer = (product) => {
        axios.post('https://flix-server.vercel.app/add-product', {product})
            .then(() => alert("Data Sent"))
            .catch(() => alert('Error sending to server'))
    };

    const handleSubmit = (e) => {
        e.preventDefault()

        if (productName.trim() === "") {
            setErrors((prevState) => ({...prevState, name: true}))
            return
        }

        if (subTitle.trim() === "") {
            setErrors((prevState) => ({...prevState, subtitle: true}))
            return
        }

        if (category.trim() === "") {
            setErrors((prevState) => ({...prevState, category: true}))
            return
        }

        if (description.trim() === "") {
            setErrors((prevState) => ({...prevState, description: true}))
            return
        }

        if (price <= 0) {
            setErrors((prevState) => ({...prevState, price: true}))
            return
        }

        if (variants.length === 0) {
            return
        }

        sendToServer(product);
        // console.log(product);
    }

    return (
        <DetailsFormContainer>
            <form>
                <h2>Product Description</h2>
                <FormControl sx={{width: "100%"}}>
                    <TextField
                        id="product-name"
                        label="Product Name"
                        variant="outlined"
                        value={productName}
                        onChange={handleNameChange}
                        error={errors.name}
                        required
                    />
                </FormControl>

                <FormControl>
                    <TextField
                        id="subtitle"
                        label="Subtitle"
                        variant="outlined"
                        value={subTitle}
                        onChange={handleTitleChange}
                        error={errors.subtitle}
                        required
                    />
                </FormControl>

                <FormControl>
                    <InputLabel htmlFor="product-price">Price</InputLabel>
                    <OutlinedInput
                        id="product-price"
                        startAdornment={<InputAdornment position="start">$</InputAdornment>}
                        label="Price"
                        type="number"
                        value={price}
                        onChange={handlePriceChange}
                        inputProps={{min: 0.1}}
                        error={errors.price}
                        required
                    />
                </FormControl>

                <FormControl sx={{minWidth: 120}}>
                    <InputLabel id="category-label">Category</InputLabel>
                    <Select
                        labelId="category-label"
                        id="category"
                        label="Category"
                        value={category || ''}
                        onChange={handleCategoryChange}
                        error={errors.category}
                        required
                    >
                        <MenuItem value="men">Men</MenuItem>
                        <MenuItem value="women">Women</MenuItem>
                        <MenuItem value="kids">Kids</MenuItem>
                    </Select>
                </FormControl>

                <FormControl>
                    <TextField
                        id="description"
                        label="Description"
                        multiline
                        minRows={4}
                        value={description}
                        onChange={handleDescriptionChange}
                        error={errors.description}
                        required
                    />
                </FormControl>

                <h2>Options</h2>
                <VariantContext.Provider value={{variants, setVariants}}>
                    {
                        variants.map((variant, i) => (<NewVariant key={i} {...variant}/>))
                    }
                    <AddVariant/>
                </VariantContext.Provider>

                <div className="action-buttons">
                    <Button type="button" onClick={props.handleClose}>Cancel</Button>
                    <Button type="submit" onClick={handleSubmit}>Save</Button>
                </div>

            </form>

        </DetailsFormContainer>

    )
};