import React from "react";
import {Close} from "@mui/icons-material";
import styled from "styled-components";
import ColorStockRow from "../../Components/StockTable";

const StockViewWrapper = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    padding-top: 144px;
    min-height: 100vh;
    width: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 600px) {
        padding-top: 127px;
    }
`;

const StockView = styled.div`
    background: #ffffff;
    padding: 0 1em 1em;
    border-radius: 15px;
    box-shadow: 1px 1px 15px rgba(0, 0, 0, .5);
    height: calc(100vh - 200px);
    overflow: scroll;
    margin: 0 5px;

    ::-webkit-scrollbar {
        display: none;
    }

    -ms-overflow-style: none;
    scrollbar-width: none;

    .stockView-top {
        position: sticky;
        top: 0;
        background: #ffffff;
        padding: 1em 0;
        border-bottom: 1px solid #d5d5d5;
        z-index: 1;
    }

    .stock-heading {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 10px;
    }

    .stock-main-info {
        display: flex;
        justify-content: space-around;
        align-items: center;
        gap: 15px;
        border-radius: 7px;
        padding: 1em;
        background: #d5d5d5;
    }

    .main-info-detail {
        color: #484848;
    }
`;

