export default function ItemStockView(props) {
    return (
        <StockViewWrapper>

            <StockView>
                <div className="stockView-top">
                    <div className="stock-heading">
                        <h2>View Stock</h2>
                        <Close onClick={props.handleClose} cursor="pointer" sx={{fontSize: "26px"}}/>
                    </div>
                    <div className="stock-main-info">
                        <div>
                            <p>Product Name</p>
                            <p className="main-info-detail">{StockData.productName}</p>
                        </div>
                        <div>
                            <p>Product ID</p>
                            <p className="main-info-detail">{StockData.id}</p>
                        </div>
                        <div>
                            <p>Price</p>
                            <p className="main-info-detail">${StockData.price}</p>
                        </div>
                    </div>
                </div>
                <div>
                    {
                        StockData.colors.map(data => <ColorStockRow {...data}/>)
                    }
                </div>
            </StockView>

        </StockViewWrapper>
    );
};

