import React, {useState} from "react";
import styled from "styled-components";
import SearchIcon from '@mui/icons-material/Search';
import {FormControl, InputBase, InputLabel, MenuItem, Select, useMediaQuery} from "@mui/material";
import AddIcon from '@mui/icons-material/Add';
import InventoryItem from "./InventoryItem";
import AddItem from "./AddItem/AddItem";

const InventoryContainer = styled.div`
    .inventory-items-container {
        padding: 1em;
    }

`;

const InventoryHeading = styled.div`
    position: sticky;
    top: 144px;
    padding: 1em 1em 0;
    background: #ffffff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid #d5d5d5;

    @media (max-width: 600px) {
        top: 127px;
        padding: 1em;

        .inventory-heading-top {
            grid-template-columns: repeat(2, 1fr) !important;
        }
    }

    h2 {
        font-weight: 300;
        font-size: 26px;
        letter-spacing: 1px;
        margin-bottom: 5px;
    }

    .inventory-heading-top {
        display: grid;
        place-items: center;
        gap: 1em;
        align-items: center;
        grid-template-columns: repeat(4, 1fr);
    }

    .inventory-items-table-heading {
        padding: 20px 0;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        place-items: center;

    }
`;

const Search = styled.div`
    grid-column: span 2;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background: #ffffff;
    box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.2);
    padding: 7px 15px;
    gap: 10px;
    border-radius: 5px;
    width: 100%;
    max-width: 450px;

    @media (max-width: 600px) {
        margin-top: 10px;
        grid-row: 1 / 3;
    }
`;

const AddNewButton = styled.button`
    display: flex;
    justify-content: space-between;
    align-items: center;
    justify-self: flex-end;
    gap: 5px;
    padding: 7px 15px;
    outline: none;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    background: hsl(140, 52%, 55%);
    transition: all .2s ease-in-out;

    &:hover {
        background: hsl(140, 52%, 48%);
    }
`;

export default function Inventory() {

    const isMobile = useMediaQuery("(max-width: 600px)");

    const [showAddItem, setShowAddItem] = useState(false);

    const openAddItem = () => {
        setShowAddItem(true)
    };

    const closeAddItem = () => {
        setShowAddItem(false)
    }

    return (
        <>
            {
                showAddItem ? <AddItem handleClose={closeAddItem}/> :
                    <InventoryContainer>

                        <InventoryHeading>
                            <h2>Inventory</h2>
                            <div className="inventory-heading-top">
                                <FormControl sx={{m: 1, minWidth: 110}}
                                             style={isMobile ? {justifySelf: "flex-start"} : {}}>
                                    <InputLabel id="select-category-label">Category</InputLabel>
                                    <Select
                                        labelId="select-category-label"
                                        id="select-category-label"
                                        autoWidth
                                        label="Category"
                                        style={{padding: 0}}
                                        sx={{
                                            color: "black",
                                            '.MuiOutlinedInput-notchedOutline': {
                                                borderColor: 'rgb(0,0,0)',
                                            },
                                            '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                                                borderColor: 'rgba(0,0,0,0.3)',
                                            },
                                            '&:hover .MuiOutlinedInput-notchedOutline': {
                                                borderColor: 'rgba(0,0,0,0.4)',
                                            },
                                        }}
                                    >
                                        <MenuItem value="all">All</MenuItem>
                                        <MenuItem value="men">Men's</MenuItem>
                                        <MenuItem value="women">Women's</MenuItem>
                                        <MenuItem value="kid">Kid's</MenuItem>
                                    </Select>
                                </FormControl>
                                <Search>
                                    <SearchIcon></SearchIcon>
                                    <InputBase placeholder="Search..." fullWidth/>
                                </Search>
                                <AddNewButton onClick={openAddItem}>
                                    <AddIcon/> Add New
                                </AddNewButton>

                            </div>
                            {
                                isMobile ? <></> :
                                    <div className="inventory-items-table-heading">
                                        <p>Name</p>
                                        <p>ID</p>
                                        <p>Category</p>
                                        <p>Price</p>
                                        <p>Action</p>
                                    </div>
                            }

                        </InventoryHeading>

                        <div className="inventory-items-container">
                            <InventoryItem/>
                            <InventoryItem/>
                            <InventoryItem/>
                            <InventoryItem/>
                            <InventoryItem/>
                            <InventoryItem/>
                            <InventoryItem/>
                            <InventoryItem/>
                            <InventoryItem/>
                        </div>

                    </InventoryContainer>
            }
        </>
    )
};