
const ImageContainer = {
    height: "300px",
    overflow: "hidden",
    marginBottom: "10px",
}

const TextContainer = {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center"
}

export default function Card(props) {
    return (
        <div>
            <div style={ImageContainer}>
                <img style={{height: "100%", width: "100%", objectFit: "cover"}} src={props.image} alt={props.alt}/>
            </div>
            <div style={TextContainer}>
                <div>
                    <p>{props.name}</p>
                    <p style={{color: "#888888"}}>{props.category}</p>
                </div>
                <p style={{fontSize: "1.5em"}}>${props.price}</p>
            </div>
        </div>
    )
}

Card.defaultProps = {
    alt: "Shoe Image"
}