import React, {useContext, useEffect, useState} from "react";
import styled from "styled-components";
import {Box, FormControl, InputLabel, OutlinedInput, Select, TextField} from "@mui/material";
import {Edit, Delete} from "@mui/icons-material";
import {VariantContext} from "./AddItem";

const ActionButtons = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
`;

const ImageStyles = {
    borderRadius: "10px",
    boxShadow: "1px 1px 10px rgba(0,0,0,0.2)",
    width: "100px",
    height: "100px",
}

export default function NewVariant(props) {

    const [size, setSize] = useState(5);

    const [qty, setQty] = useState(props.sizeQty[size]);

    const {variants, setVariants} = useContext(VariantContext);

    const handleSizeChange = (e) => {
        setSize(e.target.value);
    };

    const handleDelete = () => {
        setVariants(
            variants.filter(variant => variant.name !== props.name)
        )
    };

    useEffect(() => {
        setQty(props.sizeQty[size]);
    }, [size]);

    useEffect(() => {
        setSize(5);
        setQty(props.sizeQty[5]);
    }, [variants]);

    return (
        <div style={{borderBottom: "1px solid rgba(0,0,0,0.25)", paddingBottom: "1em"}}>
            <p>Variant: {props.name}</p>
            <Box
                component='div'
                sx={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    justifyContent: 'space-around',
                    paddingTop: '1em',
                    gap: '10px'
                }}>
                <img src={props.images.image1} alt="varient image 1" style={ImageStyles}/>
                <img src={props.images.image2} alt="varient image 1" style={ImageStyles}/>
                <img src={props.images.image3} alt="varient image 1" style={ImageStyles}/>
                <img src={props.images.image4} alt="varient image 1" style={ImageStyles}/>
            </Box>
            <Box
                component='div'
                sx={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    justifyContent: 'space-between',
                    paddingTop: '1em',
                    gap: '10px'
                }}>
                <div>
                    <FormControl sx={{m: 1, minWidth: 50, maxWidth: 120}}>
                        <InputLabel htmlFor="demo-dialog-native">Size</InputLabel>
                        <Select
                            native
                            defaultValue={size}
                            onChange={handleSizeChange}
                            input={<OutlinedInput label="Age" id="demo-dialog-native"/>}
                        >
                            {
                                Object.keys(props.sizeQty).map((key) => {
                                    return <option key={key} value={key}>{key}</option>
                                })
                            }
                        </Select>
                    </FormControl>
                    <FormControl sx={{m: 1, minWidth: 50, maxWidth: 120}}>
                        <TextField
                            label="Qty"
                            id="qty"
                            value={qty}
                            variant="standard"
                            type='number'
                            InputProps={{readOnly: true}}
                        />
                    </FormControl>
                </div>
                <ActionButtons>
                    <Delete cursor="pointer" onClick={handleDelete}/>
                </ActionButtons>
            </Box>
        </div>
    )
}