import React, {useState} from "react";
import styled from "styled-components";
import {Delete, Edit, Visibility} from "@mui/icons-material";
import {useMediaQuery} from "@mui/material";
import ItemStockView from "./ItemStockView";

const InventoryItemRow = styled.div`
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    place-items: center;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 1em 0;
    margin-bottom: 15px;

    @media (max-width: 600px) {
        display: block;

        .inventory-item-detail {
            padding: 0 1em 10px;
        }
    }

    .inventory-item-detail {
        display: flex;
        justify-content: space-between;
        gap: 1em;
    }

    .inventory-action-buttons {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
    }

    .inventory-action-buttons div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 10px;
        cursor: pointer;
    }
`;

export default function InventoryItem() {

    const isMobile = useMediaQuery("(max-width: 600px)");

    const [viewStock, setViewStock] = useState(false);

    const openStockView = () => {
        setViewStock(true);
    };

    const closeStockView = () => {
        setViewStock(false);
    };

    return (
        <React.Fragment>
            <InventoryItemRow>
                <div className="inventory-item-detail">
                    {
                        isMobile ? <p>Product Name</p> : <></>
                    }
                    <p>{"Radiant Glide"}</p>
                </div>
                <div className="inventory-item-detail">
                    {
                        isMobile ? <p>ID</p> : <></>
                    }
                    <p>{5451}</p>
                </div>
                <div className="inventory-item-detail">
                    {
                        isMobile ? <p>Category</p> : <></>
                    }
                    <p>{"Men's"}</p>
                </div>
                <div className="inventory-item-detail">
                    {
                        isMobile ? <p>Price</p> : <></>
                    }
                    <p>${150}</p>
                </div>
                <div className="inventory-action-buttons">
                    <div onClick={openStockView} style={{cursor: "pointer"}}>
                        <Visibility/>
                        {
                            isMobile ? <span>View Stock</span> : <></>
                        }

                    </div>
                    <div style={{cursor: "pointer"}}>
                        <Edit/>
                        {
                            isMobile ? <span>Edit</span> : <></>
                        }
                    </div>
                    <div style={{cursor: "pointer"}}>
                        <Delete/>
                        {
                            isMobile ? <span>Delete</span> : <></>
                        }
                    </div>
                </div>
            </InventoryItemRow>
            {
                viewStock ? <ItemStockView handleClose={closeStockView}/> : <></>
            }
        </React.Fragment>
    )
}