import React, {useEffect, useState} from "react";
import styled from "styled-components";
import {Delete} from "@mui/icons-material";
import Button from "./Button";

const CartContainer = styled.div`
    .cart-products-container {
        min-height: 100vh;
    }

    .cart-product {
        margin: 10px;
        border-radius: 15px;
        overflow: hidden;
        display: flex;
        gap: 20px;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
        box-shadow: rgba(0, 0, 0, 0.2) 0 6px 10px 0;
    }

    .cart-product-details-container,
    .cart-products-change-container {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    .cart-product-details p {
        margin-bottom: 7px;
    }

    .cart-products-change-container {
        justify-content: flex-end;
    }

    .cart-product-qty input {
        width: 40px;
        text-align: center;
    }

    .cart-product-image {
        width: 100px;
        height: 100px;
        border-radius: 10px;
        overflow: hidden;
    }

    .cart-product-image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    .cart-bottom {
        padding: 1.5em;
        position: sticky;
        bottom: 0;
        right: 0;
        width: 100%;
        text-align: center;
        background: #f7f7f7;
        box-shadow: 0 0 25px 0 rgba(0, 0, 0, .3);
    }

    .cart-total {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 1.3em;
        margin-bottom: 1em;
    }

    .cart-bottom button {
        width: 60%;
        padding: 0.7em 0;
        font-size: 16px;
    }

    .empty-cart {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        color: #6c6c6c;
        height: 100vh;
    }
`;

export default function ShoppingCart(props) {

    const handleChange = (e, currentItem) => {
        if (e.target.value > 0) {
            const newCart = items.map(item => (item === currentItem ? {...item, qty: e.target.value} : item));
            setItems(newCart)
        } else {
            e.target.value = 1
        }
    }

    const getTotal = () => {
        return items.map((item) => (item.price * item.qty)).reduce((a, c) => a + c, 0)
    }

    const [items, setItems] = useState(props.cartItems);
    const [total, setTotal] = useState(getTotal);

    useEffect(() => {
        setTotal(getTotal);
    }, [items]);

    return (
        <CartContainer>
            <div className="cart-products-container">
                {
                    items.length > 0 ?
                        items.map(
                            (item) => (
                                <div className="cart-product">
                                    <div className="cart-product-details-container">
                                        <div className="cart-product-image">
                                            <img src={item.image} alt="cart product image"/>
                                        </div>
                                        <div className="cart-product-details">
                                            <p>{item.product}</p>
                                            <p>Size: {item.size}</p>
                                            <p>${item.price}</p>
                                        </div>
                                    </div>
                                    <div className="cart-products-change-container">
                                        <div className="cart-product-qty">
                                            <input
                                                type="number"
                                                defaultValue={item.qty}
                                                min={0}
                                                onChange={
                                                    e => (handleChange(e, item))
                                                }
                                            />
                                        </div>
                                        <Delete cursor="pointer"
                                                onClick={() => setItems(items.filter(i => i !== item))}/>
                                    </div>
                                </div>
                            )
                        ) :
                        <div className="empty-cart">Your Cart is Empty</div>
                }
            </div>
            <div className="cart-bottom">
                <div className="cart-total">
                    <p>Subtotal:</p>
                    <p>${total}</p>
                </div>
                <Button primary>Checkout</Button>
            </div>
        </CartContainer>
    )
}