export default function Orders() {

    const [activeTab, setActiveTab] = useState("all");
    const [orders, setOrders] = useState(data);

    const isDesktop = useMediaQuery("(min-width: 500px)");

    useEffect(() => {
        activeTab !== "all" ? setOrders(data.filter(d => d.status === activeTab)) : setOrders(data)
    }, [activeTab]);

    return (
        <div>
            <OrdersViewController>
                <AdminHeading>Orders</AdminHeading>
                <AdminNavigation>
                    <div
                        className="admin-tab-tertiary"
                        style={(activeTab === "all") ? {borderBottom: "1px solid #000000"} : {borderBottom: "none"}}
                        onClick={() => setActiveTab("all")}
                    >
                        <span>All</span>
                        {isDesktop && <span className="count">{data.length}</span>}
                    </div>
                    <div
                        className="admin-tab-tertiary"
                        style={(activeTab === "active") ? {borderBottom: "1px solid #000000"} : {borderBottom: "none"}}
                        onClick={() => setActiveTab("active")}
                    >
                        <span>Active</span>
                        {isDesktop && <span className="count">{data.filter(d => d.status === "active").length}</span>}
                    </div>
                    <div
                        className="admin-tab-tertiary"
                        style={(activeTab === "completed") ? {borderBottom: "1px solid #000000"} : {borderBottom: "none"}}
                        onClick={() => setActiveTab("completed")}

                    >
                        <span>Completed</span>
                        {isDesktop && <span className="count">{data.filter(d => d.status === "completed").length}</span>}

                    </div>
                    <div
                        className="admin-tab-tertiary"
                        style={(activeTab === "canceled") ? {borderBottom: "1px solid #000000"} : {borderBottom: "none"}}
                        onClick={() => setActiveTab("canceled")}
                    >
                        <span>Canceled</span>
                        {isDesktop && <span className="count">{data.filter(d => d.status === "canceled").length}</span>}
                    </div>
                </AdminNavigation>
            </OrdersViewController>
            <GetOrders/>
        </div>
    )
}
