import React, {useEffect, useState} from "react";
import styled from "styled-components";
import VisibilityIcon from '@mui/icons-material/Visibility';
import OrderInfo from "./OrderInfo";
import {FormControl, InputLabel, MenuItem, Select, useMediaQuery} from "@mui/material";

const OrderItemRow = styled.div`
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    place-items: center;
    padding: 1em;
    border-radius: 15px;
    box-shadow: 4px 4px 15px rgba(0, 0, 0, .2);
    margin: 13px 0;

    @media (max-width: 650px) {
        grid-template-columns: 1fr;
        place-items: normal;
        div p {
            display: flex;
            justify-content: space-between;
            margin-bottom: 10px;
        }
    }
`;

const ActionButton = styled.button`
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    padding: 5px 10px;
    outline: none;
    border: none;
    border-radius: 5px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, .2);
    cursor: pointer;
    background: hsl(140, 52%, 55%);
    transition: all .2s ease-in-out;

    &:hover {
        background: hsl(140, 52%, 48%);
    }
`;

export default function OrderItem() {

    const isMobile = useMediaQuery("(max-width: 650px)");

    const [admin, setAdmin] = useState(false);

    useEffect(() => {
        const location = window.location.href.split('/')[window.location.href.split('/').length - 1];
        setAdmin(location === 'admin');
    }, []);

    return (
        <OrderItemRow>
            <div className="order-item-details">
                <p>
                    {
                        isMobile ? <span>Order ID</span> : <></>
                    }
                    <span>1515</span>
                </p>
            </div>
            <div className="order-item-details">
                <p>
                    {
                        isMobile ? <span>Date</span> : <></>
                    }
                    <span>{new Date().getDate()}/{new Date().getMonth()}/{new Date().getFullYear()}</span>
                </p>
            </div>
            <div className="order-item-details">
                <p>
                    {
                        isMobile ? <span>Status</span>
                            : <></>
                    }
                    {
                        admin ? <React.Fragment>
                            <FormControl>
                                <InputLabel id="select-status">Status</InputLabel>
                                <Select
                                    sx={{minWidth: "120px"}}
                                    labelId="select-status"
                                    // value={age}
                                    // onChange={handleChange}
                                    label="Status"
                                >
                                    <MenuItem value="completed">Completed</MenuItem>
                                    <MenuItem value="active">Active</MenuItem>
                                    <MenuItem value="canceled">Canceled</MenuItem>
                                </Select>
                            </FormControl>
                            </React.Fragment>
                            : <span>Completed</span>
                    }
                </p>
            </div>
            <div className="order-item-details">
                <p>
                    {
                        isMobile ? <span>Price</span> : <></>
                    }
                    <span>${150}</span>
                </p>
            </div>
            <div className="order-item-details" style={isMobile ? {justifySelf: "center"} : {}}>
                <ViewOrder/>
            </div>
        </OrderItemRow>
    )
}

function ViewOrder() {

    const [openOrder, setOpenOrder] = useState(false);

    function handleOpen() {
        setOpenOrder(true)
    }

    function handleClose() {
        setOpenOrder(false)
    }

    return (
        <React.Fragment>
            <ActionButton onClick={() => setOpenOrder(true)}><VisibilityIcon/>View Order</ActionButton>
            {openOrder ? <OrderInfo handleClose={handleClose}/> : <></>}
        </React.Fragment>
    )
}