import {useMediaQuery} from "@mui/material";
import React from "react";
import styled from "styled-components";

const StockContainer = styled.div`
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    border-bottom: 1px solid #d5d5d5;
    overflow-x: scroll;

    @media screen and (max-width: 775px) {
        flex-direction: column;
        margin-bottom: 25px;
        padding-bottom: 25px;
    }
`;

const StockImage = styled.div`
    max-width: 100px;
    max-height: 100px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 5px;
    flex-shrink: 0;
    position: sticky;
    left: 0;
    
    @media screen and (max-width: 775px) {
        max-width: 200px;
        max-height: 200px;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
`;

const StockTable = styled.table`
    border-spacing: 0;
    border: 1px solid #d5d5d5;
    width: 100%;
    flex-grow: 0;

    th, td {
        text-align: center;
        padding: 0.5em;
        min-width: 50px;
        border: 1px solid #d5d5d5;
    }
`;

export default function ColorStockRow(props) {

    const isMobile = useMediaQuery("(max-width: 775px)");

    return (
        <StockContainer>
            <StockImage>
                <img src={props.image} alt="stock image"/>
            </StockImage>
            {
                isMobile ? <MobileTable sizeStock={props.sizeStock}/> : <DesktopTable sizeStock={props.sizeStock}/>
            }
        </StockContainer>

    );
}


function DesktopTable(props) {
    return (
        <StockTable>
            <tr>
                <th>Size</th>
                {
                    props.sizeStock.map((item) => {
                        return <td>{Object.keys(item)[0]}</td>
                    })
                }
            </tr>
            <tr>
                <th>Qty</th>
                {
                    props.sizeStock.map((item) => {
                        return <td>{Object.values(item)[0]}</td>
                    })
                }
            </tr>
        </StockTable>
    )
}

function MobileTable(props) {
    return (
        <StockTable>
            <tr>
                <th>Size</th>
                <th>Qty</th>
            </tr>
            {
                props.sizeStock.map((item) => {
                    return (
                        <tr>
                            <td>{Object.keys(item)[0]}</td>
                            <td>{Object.values(item)[0]}</td>
                        </tr>
                    )
                })
            }
        </StockTable>
    )
}